.contact-section .container {
  width: 100%;
  background: linear-gradient(transparent 345px, var(--base-bg-color) 0);
  @media screen and (min-width: 768px) {
    background: linear-gradient(transparent 304px, var(--base-bg-color) 0);
    width: 100%;
  }
  @media screen and (min-width: 1200px) {
    background: linear-gradient(transparent 96px, var(--base-bg-color) 0);
  }
}
.contact {
  padding-bottom: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  // margin-top: 393px;

  // @media screen and (min-width: 768px) {
  //   margin-top: 304px;
  // }

  // @media screen and (min-width: 1200px) {
  //   margin-top: 196px;
  // }
}
// .contact__container {
//   margin-top: -345px;
// }
.contact__item {
  display: flex;
  flex-direction: column;
  width: 280px;
  margin-bottom: 30px;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-right: 32px;
  padding-left: 32px;
  background: rgba(252, 252, 252, 1);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
}

.contact__title {
  font-weight: 500;
  font-size: 12px;
  line-height: 186%;
}

.contact__adress {
  position: relative;
  font-weight: 500;
  font-size: 12px;
  line-height: 183%;
  letter-spacing: 0.04em;
  color: rgba(214, 147, 109, 1);
}

.contact__city-adress {
  position: relative;
  display: flex;
  flex-direction: column;
}

.contact__line {
  display: inline-block;
  position: absolute;
  width: 100%;
  max-width: 216px;
  content: '';
  border: 1px solid #e1e1e1;
  bottom: -24px;
  left: 0px;
}

.contact__city {
  font-weight: 500;
  font-size: 12px;

  line-height: 186%;
  letter-spacing: 0.04em;
  margin-bottom: 8px;
}

.contact__name {
  width: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 1px;
  padding-bottom: 1px;
  margin-bottom: 20px;
  background: #f8d24e;
  font-size: 12px;
  transition: all 150ms cubic-bezier(0.5, 0.55, 0.63, 0.54);

  @media screen and (min-width: 768px) {
    font-size: 16px;
    margin-bottom: 32px;
  }

  border-radius: 4px;
  display: flex;
  justify-content: center;
  line-height: 186%;
  letter-spacing: 0.04em;
  border: 2px solid transparent;
  cursor: default;

  &:hover {
    background-color: transparent;
    border: 2px solid #0a8817;
  }
}

.contact__time {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 12px;
  line-height: 186%;
  color: rgba(171, 171, 171, 1);
}

.contact__time:last-child {
  margin-bottom: 0px;
}

.contact__block {
  display: flex;
  flex-direction: column;

  &:first-child {
    margin-bottom: 8px;
  }
}

.contact__opening-hours {
  position: relative;
  margin-top: 48px;
  display: flex;
  flex-direction: column;
}

.contact__mail-phone {
  margin-top: 48px;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #131313;
  text-decoration: none;
  @media screen and (min-width: 1200px) {
    display: flex;
    flex-direction: column;
  }
}

.phone-mail__item {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #131313;
  text-decoration: none;
  cursor: pointer;

  &:hover,
  &:focus {
    color: blue;
  }
}

.contact__button {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.contact__button-item {
  align-items: center;
  background: #f8d24e;
  border-radius: 10px;
  border: 2px solid transparent;
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.28;
  position: relative;
  -webkit-transition: all 150ms ease-in-out;
  transition: all 150ms cubic-bezier(0.23, 0.56, 0.74, 0.09);

  @media screen and (max-width: 280px) {
    min-height: 64px;
  }
  &:last-child {
    margin-bottom: 0px;
    border: 2px solid transparent;
  }

  &:hover {
    background-color: rgba(10, 136, 23, 1);
    color: white;
  }

  &:focus {
    color: rgba(252, 252, 252, 1);
    background-color: rgba(10, 136, 23, 1);
  }

  &:disabled {
    background-color: rgba(171, 171, 171, 1);
    border: 2px solid transparent;
  }
}

.contact__button-item-second {
  border: 2px solid var(--accent-color);
  border-radius: 10px;
  padding: 12px 46px;
  background-color: transparent;
  // position: relative;
  // border-radius: 10px;
  // padding-top: 12px;
  // padding-bottom: 12px;
  // padding-left: 34px;
  // padding-right: 52px;
  // outline: 3px solid;
  // outline-color: rgb(248, 210, 78);
  // outline-offset: 0px;
  // border: 0px;
  transition: all 150ms cubic-bezier(0.23, 0.56, 0.74, 0.09);
  // background-color: transparent;
  font-weight: 600;
  font-size: 14px;
  line-height: calc(18 / 14);
  @media screen and (max-width: 280px) {
    min-height: 64px;
  }
  &:hover {
    border: 2px solid rgb(10, 138, 23);
  }

  &:focus {
    border: 2px solid rgb(10, 138, 23);
    color: var(--second-bg-color);
  }

  &:disabled {
    border: 2px solid rgba(171, 171, 171, 1);
  }
}

// адаптивка

@media screen and (min-width: 768px) {
  .contact__container {
    display: flex;
    margin-bottom: 60px;
    // margin-top: -304px;
  }

  @media screen and (max-width: 1200px) {
    .contact__city {
      margin-bottom: 12px;
    }
  }

  .contact__item {
    width: 224px;
    margin-bottom: 0px;
    margin-right: 16px;
    padding-left: 24px;
    padding-right: 24px;
  }

  .contact__item:last-child {
    margin-right: 0px;
  }

  .contact__opening-hours {
    margin-top: 64px;
  }

  .contact__line {
    bottom: -32px;
  }

  .contact__mail-phone {
    margin-top: 64px;
  }

  // font size
  .contact__adress,
  .contact__time,
  .contact__title,
  .phone-mail__item,
  .contact__city {
    font-size: 14px;
  }
}

@media screen and (min-width: 1200px) {
  .contact__container {
    margin-bottom: 64px;
    // margin-top: -96px;
  }

  .contact__item {
    padding-left: 40px;
    width: 368px;
  }

  // font size
  .contact__city,
  .contact__adress,
  .contact__title,
  .contact__time,
  .phone-mail__item {
    font-size: 16px;
  }
}
.contact-button__svg {
  width: 12px;
  height: 12px;
  position: absolute;
  top: 15px;
  right: 32px;
  @media screen and (max-width: 280px) {
    top: 24px;
  }

  &:focus {
    fill: rgba(252, 252, 252, 1);
  }
}
.contact__button-item:focus .contact-button__svg {
  fill: rgba(252, 252, 252, 1);
}
.contact .contact__button {
  @media screen and (min-width: 768px) {
    display: flex;
    margin-bottom: 0px;
    flex-direction: row;
  }
}

@media screen and (min-width: 678px) {
  .contact .contact__button-item {
    margin-bottom: 0px;
    // &:first-child {
    //   margin-right: 15px;
    // }
  }
}
.contact
  .contact__button-item:hover
  ~ .contact__button-item-second
  .contact-button__svg {
  transition: all 150ms cubic-bezier(0.5, 0.55, 0.63, 0.54);
}
.contact
  .contact__button-item:hover
  ~ .contact__button-item-second
  .contact-button__svg {
  top: 23px;
  transition: all 150ms cubic-bezier(0.5, 0.55, 0.63, 0.54);
}
.contact__button-item-second .contact-button__svg {
  top: 18px;
}
